























































import { Component, Vue, Prop } from 'vue-property-decorator';
import SendIcon from './icons/SendIcon.vue';
import { emailClient } from './smtpEmail';

@Component({
  components: {
    SendIcon
  },
  name: 'section-contact-form'
})
export default class SectionContactForm extends Vue {
  async sendEmail() {
    const name = this.$refs.name as HTMLInputElement;
    const email = this.$refs.email as HTMLInputElement;
    const message = this.$refs.message as HTMLTextAreaElement;

    if (this.isFormValid(name, email, message)) {
      try {
        await emailClient.send({
          SecureToken: 'c73c966d-fa5d-4c26-94ef-6e1f478d195d',
          To: 'marius@bongarts.net',
          From: email.value,
          Subject: `Kontaktanfrage von ${name.value}`,
          Body: message.value
        });
        alert(
          'Danke fr Ihre Anfrage! Ich werde mich schnellstmglich bei Ihnen melden.'
        );
        name.value = '';
        email.value = '';
        message.value = '';
      } catch (error) {
        alert('Something went wrong');
      }
    }
  }

  isFormValid(
    name: HTMLInputElement,
    email: HTMLInputElement,
    message: HTMLTextAreaElement
  ) {
    return (
      name.validity.valid && email.validity.valid && message.validity.valid
    );
  }
}
